       h2 1.1 Principales conceptos asociados al sistema de potabilizacin

    .row.justify-content-center.align-items-center.mb-5
      p El sistema de acueducto se encuentra conformado por diferentes etapas que buscan suministrar a la poblacin de manera continua el agua requerida para su consumo y dems actividades que involucren el uso de este recurso. 


    .bg1-tema1

      .row.justify-content-center.align-items-center.mb-4
        .col-lg-3
          figure
            img(src='@/assets/curso/temas/tema1/img3.png', alt='Imgen decorativa')

        .col-lg-9
          p En trminos generales, es posible definir el sistema de acueducto como el conjunto de estructuras que permiten la captacin de agua, transporte y distribucin a los usuarios, cumpliendo con unas caractersticas mnimas para su consumo.

          p El Reglamento Tcnico del Sector de Agua Potable y Saneamiento Bsico (RAS), en su Ttulo B (2010), permite identificar los componentes que conforman el sistema de acueducto y la normatividad relacionada con el suministro de agua, estos son:

      .row
        .col-lg-7
