          p Consisten en todas las aguas provenientes de cursos o cuerpos superficiales o subterrneos. Tambin pueden considerarse como fuentes, en casos excepcionales, las aguas lluvias y el agua de mar. 

          .cajon.color-gris2.p-4.mb-3
            p.mb-0 La seleccin de la fuente debe realizarse basndose en la calidad del agua, la ocurrencia de eventos de sequa y escogiendo aquella que permita la construccin de una captacin econmica, segura, confiable y que tenga unas caractersticas de acceso, operacin y mantenimiento fciles. 

          p Adems, deben efectuarse estudios con el fin de minimizar los impactos sobre el medio ambiente, el ecosistema y el hbitat natural de diferentes especies, que puedan producir las obras de la captacin. 

          p En particular, se debe conocer el caudal ecolgico en la fuente de agua definido por la autoridad ambiental competente para la estimacin de la capacidad utilizable de la fuente.

        .col-lg-5
          figure
            img(src='@/assets/curso/temas/tema1/img4.svg', alt='Imgen decorativa')        

      .row
