            .cajon.color-gris2.p-3.mb-3
              p.mb-0 El objetivo primordial de los tanques de compensacin es cubrir las necesidades de la demanda de agua en los momentos pico, permitiendo una recuperacin del volumen en las horas de bajo consumo para poder suministrar, sin problema, el agua demandada en las horas de mximo consumo.

