            p.my-0.my-lg-5.text-center.text-small Las lneas de aduccin del acueducto son los conductos destinados a transportar por gravedad o por bombeo las aguas crudas desde los sitios de captacin hasta las plantas de tratamiento, prestando excepcionalmente servicio de suministro de agua cruda a lo largo de su longitud. Las lneas de conduccin son aquellas destinadas al transporte de agua tratada desde la planta de tratamiento hasta los tanques de almacenamiento o hasta la red de distribucin, generalmente sin entrega de agua en ruta.

