    #t_1_2.titulo-segundo.color-acento-contenido
       h2 1.2 Tipos de agua

      
    .row.justify-content-center.align-items-center.mb-5
      p Existen diferentes tipos de agua segn sus propiedades y su estado, esto es conocido como Ciclo hidrolgico o ciclo del agua, los cuales segn el RAS 2000 en su ttulo B, define de la siguiente manera:

    .row
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Agua cruda  </span>

    .row.justify-content-center.align-items-center.mb-3
      p Es el agua natural que no ha sido sometida a proceso de tratamiento para su potabilizacin.

    .row.justify-content-center.mb-5
      p #[b El RAS 2000 en su ttulo B], establece que durante la operacin del pozo debe realizarse un muestreo de la calidad del agua cruda segn las siguientes disposiciones:

      .col-lg-4.mb-3
        figure
          img(src="@/assets/curso/temas/tema1/img13.svg", alt="Imgen decorativa")

      .col-lg-8
        TabsA.color-pestana-azul
          .tarjeta.tarjeta--gris.p-4(titulo="Nivel de complejidad bajo")
            p Para el nivel de complejidad del sistema bajo debe realizarse un muestreo como mnimo cada mes, el cual debe incluir: coliformes totales y fecales y demanda bioqumica de oxgeno (DBO).

          .tarjeta.tarjeta--gris.p-4(titulo="Nivel de complejidad medio")
            p Para el nivel de complejidad del sistema medio debe realizarse un muestreo como mnimo cada semana, el cual debe incluir: coliformes totales y fecales, demanda bioqumica de oxgeno, slidos suspendidos totales, pH, alcalinidad y dureza.

          .tarjeta.tarjeta--gris.p-4(titulo="Nivel de complejidad alto y medio alto")
            p Para los niveles de complejidad del sistema alto y medio alto debe realizarse un muestreo diario, el cual debe incluir, adems de lo exigido en los otros niveles, fluoruros, cloruros, hierro, alcalinidad total y dureza.


    .row.justify-content-center.align-items-center.mb-5
      .col-lg-7
        p Los muestreos para determinar la calidad del agua deben ajustarse a #[b las Normas Tcnicas NTC ISO 5667-3, NTC ISO 5667-11, GTC 30 y AWWA A-100, Seccin 12.] Todos los registros de los resultados de las muestras deben guardarse y tenerse a disposicin de la SSPD.

        p En caso de que la calidad del agua sea inferior a la mnima establecida en las normas ambientales correspondientes, la entidad encargada de la operacin debe tomar las medidas de control de contaminacin necesarias y adelantar un programa de recuperacin del acufero.

        .cajon.color-azul.p-4.mb-3
          p.mb-0 El RAS, tambin especifica que se debe tener seguridad en la calidad de las aguas crudas, para la eleccin de una fuente superficial, el consultor debe realizar el estudio de identificacin de posibles fuentes de contaminacin, valorar los riesgos y definir si la fuente es apta o no, por lo que se deben considerar las siguientes observaciones:

      .col-lg-5
        figure
          img(src="@/assets/curso/temas/tema1/img14.svg", alt="Imgen decorativa")      


    .row.justify-content-center.align-items-center.mb-5
      .tarjeta.tarjeta--gris.p-4
        SlyderA(tipo="b")
          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6.mb-3
              figure
                img(src="@/assets/curso/temas/tema1/img15.png", alt="Imgen decorativa")

            .col-md-6.mb-4.mb-md-0
              p En las captaciones hechas en ros, las aguas tienden a ser turbias, algunas veces coloreadas y en la gran mayora de los casos reciben la descarga de aguas residuales, tanto domsticas como industriales que se han vertido aguas arriba. Las fuentes que toman aguas de lagos son ms claras que las aguas de ros, pero tambin estn sujetas a la contaminacin.

          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6.mb-4.mb-md-0
              p Las fuentes que toman aguas de lagos son ms claras que las aguas de ros, pero tambin estn sujetas a la contaminacin.

            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema1/img16.svg', alt='Imgen decorativa')

          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6.mb-3
              figure
                img(src="@/assets/curso/temas/tema1/img17.png", alt="Imgen decorativa")

            .col-md-6.mb-4.mb-md-0
              p Las fuentes de agua localizadas en ros pequeos y en quebradas de montaa son limpias y puras; en estado natural son apropiadas para el consumo humano. No obstante, estas aguas estn expuestas a contaminacin por accin eventual. 

          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6.mb-4.mb-md-0
              p Por consiguiente, no pueden considerarse potables a menos que se tomen las medidas apropiadas para su proteccin: instalacin de plantas de tratamiento, vigilancia de la cuenca, colocacin de carteles o letreros y/o cercas para impedir la invasin de personas y/o animales.

            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema1/img18.png', alt='Imgen decorativa')

          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6.mb-3
              figure
                img(src="@/assets/curso/temas/tema1/img19.svg", alt="Imgen decorativa")

            .col-md-6.mb-4.mb-md-0
              p En todos aquellos casos en que se proyecten lagos artificiales mediante la construccin de embalses, deben tenerse en cuenta las condiciones futuras de la calidad del agua almacenada. 

          .row.justify-content-center.align-items-center.my-lg-4
            .col-md-6.mb-4.mb-md-0
              p El consultor debe tener en cuenta el Decreto 1575 de 2007 en lo referente a las caractersticas fsicas, qumicas y microbiolgicas del agua y el mapa de riesgo de calidad de agua en todos los casos.

            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema1/img20.svg', alt='Imgen decorativa')


    .row.justify-content-center.align-items-center.mb-5(data-aos="flip-down")
      .col-lg-10
        .tarjeta.bgDescarga.p-3.mb-5
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/temas/tema1/img21.svg",alt="Imgen decorativa")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.texto-blanco.mb-3 Reglamento tcnico del sector de agua y saneamiento. Ttulo B
                  p.texto-blanco Para conocer e identificar todos los lineamientos sobre aguas crudas, lo invitamos a leer el documento Sistemas de acueducto, el cual se encuentra en el material complementario.
                .col-sm-auto
                  a.boton.color-primario(href="https://www.minvivienda.gov.co/sites/default/files/documentos/titulob-030714.pdf" target="_blank")
                    span.texto-negro Descargar
                    i.fas.fa-file-download.texto-negro

    .row
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Agua potable</span>


    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8
        p Es aquella que, por cumplir las caractersticas fsicas, qumicas y microbiolgicas, en las condiciones sealadas en el decreto 1575 de 2007 y dems normas que la reglamenten, es apta para consumo humano. Se utiliza en bebida directa, en la preparacin de alimentos o en la higiene personal.

        .cajon.color-azul.p-4
          p.mb-0 La siguiente tabla presenta el valor mximo aceptable de las caractersticas fsicas, qumicas y microbiolgicas del agua para consumo humano.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema1/img22.png', alt='Imgen decorativa')      


    .row.justify-content-center.align-items-center.mb-5
      .col-lg-9

        .titulo-sexto.color-adicional-1.mb-5
          h5 Tabla 1.
          span Valores mximos aceptables dependiendo de la caracterstica del recurso. 

        .tabla-a.color-adicional-1
          table(alt="Muestras las caracteristicas fsicas y el valor mximo aceptable.")
            caption Fuente: Aguas de occidente. (s.f). Preguntas frecuentes sobre la calidad del agua en los sistemas operados por aguas regionales EPM S.A. E.S.P [Tabla] (p.5). 
            thead
                <th colspan="2">Resolucin 2115 de 2007</th>
            tbody
              tr
                th Caractersticas fsicas
                th Valor mximo aceptable
              tr(style='background-color: #F5FBFF')
