            p.mb-0 Para determinar si un agua se puede consumir, se utiliza el ndice de Riesgo de la Calidad del Agua para Consumo Humano (IRCA), que es el grado de riesgo de ocurrencia de enfermedades relacionadas con el no cumplimiento de las caractersticas fsicas, qumicas y microbiolgicas del agua para consumo humano. El clculo de dicho ndice debe realizarse a cada una de las muestras de agua potable recolectadas en la red de distribucin y posteriormente se determina un ndice promedio mensual para el sistema de acueducto.

    .row.mb-5
      p La siguiente tabla nos presenta los niveles de riesgo de acuerdo con la clasificacin IRCA y el significado de cada uno de esos niveles.

