       h2 1.4 Normatividad para agua potable: conceptos, aplicacin

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10
        .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-4
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/temas/tema1/img24.svg')})`}"
          )(alt="Imgen decorativa")
          .bloque-texto-g__texto.p-3
            p.mb-0 Durante los diferentes contenidos se ha recalcado la importancia de conocer, aplicar y cumplir el marco normativo de referencia para el desarrollo de las actividades inherentes a los sistemas de potabilizacin y saneamiento. A continuacin se presenta una relacin de las principales normas aplicables en el contexto de agua potable y algunos de sus artculos.


    .bg2-tema1
      .row.justify-content-center.align-items-center.mb-5
        .col-2.col-lg-1
          figure
            img(src='@/assets/curso/temas/tema1/img25.svg', alt='Imgen decorativa') 

        .col-10.col-lg-11
          p #[b El Decreto 1575 de 2007], establece el Sistema para la Proteccin y Control de la Calidad del Agua para Consumo Humano con los artculos que se exponen a continuacin: 

      .row.justify-content-center.align-items-center.mb-5
        TabsC.color-acento-contenido
          .py-3.py-md-4(titulo="Artculo 1. Objeto y campo de aplicacin")
            .row
              .col-md-6.mb-4.mb-md-0

                p #[b Objeto.] Establecer el sistema para la proteccin y control de la calidad del agua, con el fin de monitorear, prevenir y controlar los riesgos para la salud humana causados por su consumo, exceptuando el agua envasada.

                p #[b Aplicacin.] Todas las personas prestadoras que suministren o distribuyan agua para consumo humano, ya sea cruda o tratada, en todo el territorio nacional, independientemente del uso que de ella se haga para otras actividades econmicas, a las direcciones territoriales de salud, autoridades ambientales y sanitarias y a los usuarios. 

            
              .col-md-6
                figure
                  img(src='@/assets/curso/temas/tema1/img26.png', alt='Imgen decorativa')

          .py-3.py-md-4(titulo="Artculo 4. Responsables.")
            .row
              .col-md-6

                p La implementacin y desarrollo de las actividades de control y calidad del agua para consumo humano, ser responsabilidad de los Ministerios de la Proteccin Social y de Ambiente, Vivienda y Desarrollo Territorial, la Superintendencia de Servicios Pblicos Domiciliarios, el Instituto Nacional de Salud, las Direcciones Departamentales Distritales y Municipales de Salud, las personas prestadoras que suministran o distribuyen agua para consumo humano y los usuarios, para lo cual cumplirn las funciones indicadas en los artculos siguientes.  

              .col-md-6.mb-4.mb-md-0
                figure
                  img(src='@/assets/curso/temas/tema1/img27.png', alt='Imgen decorativa')

          .py-3.py-md-4(titulo="Artculo 9. Responsabilidades de las personas prestadoras.")
            .row
              .col-md-6

                p Realizar el control de las caractersticas fsicas, qumicas y microbiolgicas del agua para consumo humano, como tambin de las caractersticas adicionales definidas en el mapa de riesgo o lo exigido por la autoridad sanitaria de la jurisdiccin. 

              .col-md-6.mb-4.mb-md-0
                figure.mb-3
                  img(src='@/assets/curso/temas/tema1/img28.png', alt='Imgen decorativa')

              p Lavar y desinfectar antes de la puesta en funcionamiento y como mnimo dos (2) veces al ao, los tanques de almacenamiento de aguas tratadas. 

              p Lavar y desinfectar, antes de ponerlos en operacin y cada vez que se efecten reparaciones en ellos, los pozos profundos y excavados a mano para captacin de agua subterrnea, las estructuras de potabilizacin y las tuberas de distribucin de agua para consumo humano.

              p.mb-0 Drenar peridicamente en aquellos puntos de la red de distribucin que presentan zonas muertas o de baja presin.

              p Cuando la persona prestadora que suministra o distribuye agua para consumo humano preste el servicio a travs de medios alternos como son carrotanques, pilas pblicas y otros, se debe realizar el control de las caractersticas fsicas, qumicas y microbiolgicas del agua; como tambin de las caractersticas adicionales definidas en el mapa de riesgo o lo exigido por la autoridad sanitaria de la jurisdiccin, segn se establezca en la reglamentacin del presente decreto. 


    .row.justify-content-center.align-items-center.mb-5
      .col-2.col-lg-1
        figure
          img(src='@/assets/curso/temas/tema1/img25.svg', alt='Imgen decorativa') 

      .col-10.col-lg-11
        p #[b Resolucin 2115 de 2007:] por medio de la cual se sealan caractersticas, instrumentos bsicos y frecuencias del sistema de control y vigilancia para la calidad del agua para consumo humano, veamos a mayor detalle lo que indica esta resolucin:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.mb-3
        .row.justify-content-center.align-items-center
          .col-lg-10.dotted-border.p-4
            .row.justify-content-center.align-items-center
              .col-lg-10.mb-3
                figure
                  img(src='@/assets/curso/temas/tema1/img33.svg', alt='Imgen decorativa')

              p.text-center Caractersticas fsicas y qumicas del agua para consumo humano (valores mximos aceptables). 

      .col-lg-7.dotted-border.p-3.mb-3
        p #[b Artculo 14.]  Clculo del IRCA.
        p #[b Artculo 11.]  Caractersticas microbiolgicas (valores mximos aceptables).
        p #[b Artculo 15.]  Clasificacin de nivel de riesgo.
        p #[b Artculo 21.]  Frecuencias y nmero de muestras de control de la calidad fsica y qumica del agua para consumo humano que debe ejercer la persona prestadora.
        p.mb-0 #[b Artculo 22.]  Frecuencias y nmero de muestras de control de la calidad microbiolgica del agua para consumo humano que debe ejercer la persona prestadora.





    .bg2-tema1
      .row.justify-content-center.align-items-center.mb-5
        .col-2.col-lg-1
          figure
            img(src='@/assets/curso/temas/tema1/img25.svg', alt='Imgen decorativa') 

        .col-10.col-lg-11
          p #[b Resolucin 811 de 2008:] por medio de la cual se definen los lineamientos a partir de los cuales la autoridad sanitaria y las personas prestadoras, concertadamente definirn en su rea de influencia los lugares y puntos de muestreo para el control y la vigilancia de la calidad del agua para consumo humano en la red de distribucin, Revisemos lo que describe esta resolucin:

      .row.justify-content-center.align-items-center.mb-5
        .col-lg-4
          .tarjeta-avatar-b.mb-5
            .tarjeta-avatar-b__img
              img(src='@/assets/curso/temas/tema1/img30.svg' alt='Imgen decorativa')
            .tarjeta.tarjeta-blanca
