                h4 Artculo 2
                p Criterios para puntos de recoleccin de muestras en red de distribucin.

        .col-lg-4
          .tarjeta-avatar-b.mb-5
            .tarjeta-avatar-b__img
              img(src='@/assets/curso/temas/tema1/img31.svg' alt='Imgen decorativa')
            .tarjeta.tarjeta-blanca
