          p.mb-0 Para conocer los procesos y operaciones unitarias que se deben tener en cuenta para la potabilizacin de agua, el Ministerio de Vivienda en el RAS 2000, ttulo A, define los niveles de complejidad de los sistemas de acuerdo con el nmero de habitantes en la zona urbana del municipio, su capacidad econmica y el grado de exigencia tcnica que se requiera para adelantar el proyecto.

        p Para todo el territorio nacional se establecen los siguientes niveles de complejidad:

        .row.justify-content-center.align-items-center
