        p Fuente: Ministerio de Desarrollo Econmico. (2010). Reglamento tcnico del sector de Agua potable y Saneamiento bsico [RAS]. Ttulo A [Tabla]. (p.9).

    .row.justify-content-center.align-items-center.mb-5(data-aos="flip-down")
      .col-lg-10
        .tarjeta.bgDescarga.p-4
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/temas/tema1/img21.svg",alt="Imgen decorativa")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.texto-blanco.mb-3 Reglamento tcnico del sector de agua y saneamiento Titulo A.
                  p.texto-blanco Con el propsito de conocer e identificar los componentes, los niveles de complejidad te invitamos a leer el documento, Aspectos generales de los sistemas de agua potable y saneamiento bsico, que se encuentra en el material complementario.
                .col-sm-auto
                  a.boton.color-primario(href="https://minvivienda.gov.co/sites/default/files/documentos/010710_ras_titulo_a_.pdf " target="_blank")
                    span.texto-negro Descargar
                    i.fas.fa-file-download.texto-negro

    .row.justify-content-center.align-items-center.mb-5
      p #[b De acuerdo con el RAS 2000, ttulo C], los procesos que conforman un sistema convencional de potabilizacin del agua son: captacin del agua, remocin de material flotante y slidos gruesos mediante rejillas, pretratamiento, coagulacin, floculacin, sedimentacin, filtracin, desinfeccin, estabilizacin y distribucin del agua al sistema de redes de acueducto (p.75). 

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8

        .titulo-sexto.color-adicional-1.mb-5
          h5 Figura 1.
          span Sistema convencional de tratamiento de agua potable.

        figure
          img(src='@/assets/curso/temas/tema1/img40.svg', alt='Muestra un sistema convencional iniciando por fuente, rejillas, pretratamiento, coagulacin, floculacin, sedimentacin, filtracin, desinfeccin, estabilizacin y distribucin.')
          
          figcaption.mt-3 Fuente: Ministerio de Desarrollo Econmico. (2010). Reglamento tcnico del sector de Agua potable y Saneamiento bsico [RAS]. ttulo

    .row.bg-titulo4.align-items-center.p-1.mb-4
      h3.text-white.my-3.texto-derecha Rejillas

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.mb-4
        figure
          img(src='@/assets/curso/temas/tema1/img41.svg', alt='Imgen decorativa')

      .col-lg-8
        p #[b De acuerdo con el RAS 2000], ttulo B, la captacin de aguas superficiales a travs de rejillas se utiliza especialmente en los ros de zonas montaosas, los cuales estn sujetos a grandes variaciones de caudal entre los perodos de estiaje y los perodos de crecientes mximas. El elemento base del diseo es la rejilla de captacin, la cual debe ser proyectada con barras transversales o paralelas a la direccin de la corriente. Los otros tipos de toma tambin deben tener rejillas, con el fin de limitar la entrada de material flotante hacia las estructuras de captacin.

        .row
          .col-lg-7
            .list.mb-3(data-aos="fade-down")
              .list__item
                .list__item__icon
                  img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
              .list__txt
                h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Elementos de diseo de las rejillas</span>

        p En todo diseo de rejillas deben contemplarse los siguientes elementos: el caudal correspondiente al nivel de aguas mnimas en el ro, el caudal requerido por la poblacin que se va a abastecer y el nivel mximo alcanzado por las aguas durante las crecientes, con un perodo de retorno mnimo de 20 aos.  Le invitamos a que revise este tema con mayor profundidad a continuacin:


    .row.justify-content-center.align-items-center.mb-5
      .tarjeta--container.row.mb-5
        .col-md.tarjeta.color-acento-contenido.p-4
          .row.justify-content-center.align-items-center.mb-4
            .col-6
              figure
                img(src='@/assets/curso/temas/tema1/img42.svg', alt='Imgen decorativa')
            
          h2.text-center.mt-5 Inclinacin de las rejillas
          p.text-center.mb-0 En el caso de rejillas utilizadas para la captacin de aguas superficiales en cursos de agua de zonas montaosas, la rejilla debe estar inclinada entre 10% y 20% hacia la direccin aguas abajo. En el caso de otros tipos de estructuras de captacin, las rejillas deben tener una inclinacin de 70 a 80 con respecto a la horizontal.
        
        .col-md.tarjeta.color-primario.text-white.p-4
          .row.justify-content-center.align-items-center.mb-4
            .col-6
              figure
                img(src='@/assets/curso/temas/tema1/img43.svg', alt='Imgen decorativa')
            
          h2.text-center.mt-5 Separacin entre barrotes
          p.text-center.mb-0 Separacin entre barrotes: la separacin entre barrotes, para el caso de estructuras de captacin en ros con gravas gruesas, debe ser entre 75 mm y 150 mm. Para ros caracterizados por el transporte de gravas finas, la separacin entre barrotes debe ser entre 20 mm y 40 mm.
        
        .col-md.tarjeta.color-secundario.text-white.p-4
          .row.justify-content-center.align-items-center.mb-4
            .col-6
              figure
                img(src='@/assets/curso/temas/tema1/img44.svg', alt='Imgen decorativa')
            
          h2.text-center.mt-5 Ancho de la rejilla 
          p.text-center.mb-0 Ancho de la rejilla: el ancho de la rejilla debe depender del ancho total de la estructura de captacin y el caudal mnimo de la fuente (p.71).


    .row.bg-titulo4.align-items-center.p-1.mb-4
      h3.text-white.my-3.texto-derecha Desarenador

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.mb-4
        figure
          img(src='@/assets/curso/temas/tema1/img45.svg', alt='Imgen decorativa')

      .col-lg-8
        p #[b De acuerdo con el RAS 2000, ttulo B], es un componente destinado a la remocin de las arenas y slidos que estn en suspensin en el agua, mediante un proceso de sedimentacin. Este debe instalarse en el primer tramo de la aduccin, lo ms cerca posible a la captacin de agua, de acuerdo con las condiciones topogrficas y geolgicas de la zona.

      .cajon.color-gris.p-4
        p Preferiblemente, los desarenadores deben ser del tipo autolimpiante. Los desarenadores deben contener canales o pasos directos para su operacin mientras se efecta el mantenimiento. Los elementos que debe contener el diseo de un desarenador son los siguientes:

    
    .row.justify-content-center.align-items-center.mb-5
      ImagenInfografica.color-primario
        template(v-slot:imagen)
          figure
            img(src='@/assets/curso/temas/tema1/img46.svg', alt='Imgen decorativa')

        .tarjeta.color-acento-contenido.p-3(x="3.9%" y="37%" numero="+")
          p Deben proyectarse los dispositivos de entrada y salida de modo que aseguren una buena distribucin transversal del flujo y se reduzca a un mnimo la posibilidad de corto circuito dentro del desarenador. La velocidad de paso en los orificios de la pantalla de entrada ser inferior a 0.10 m/s. 

        .tarjeta.color-acento-contenido.p-3(x="7.8%" y="100%" numero="+")
          p La tubera o canal de llegada debe colocarse en el eje longitudinal del desarenador. Igual sucede en el caso de un canal situado aguas arriba del desarenador.

        .tarjeta.color-acento-contenido.p-3(x="28%" y="27%" numero="+")
          p En la entrada debe instalarse un dispositivo para distribuir uniformemente el flujo a lo ancho de la seccin transversal del desarenador. 

        .tarjeta.color-acento-contenido.p-3(x="44.6%" y="31%" numero="+")
          p El dispositivo de salida debe tener un canal recolector provisto de un vertedero que asegure una distribucin uniforme del flujo en toda la seccin transversal del desarenador. 

        .tarjeta.color-acento-contenido.p-3(x="35.8%" y="60%" numero="+")
          p El dispositivo de limpieza debe ubicarse en el rea de almacenamiento.

        .tarjeta.color-acento-contenido.p-3(x="35.7%" y="86%" numero="+")
          p El dispositivo de rebose debe tener un vertedero lateral ubicado cerca de la entrada del desarenador.  

        .tarjeta.color-acento-contenido.p-3(x="50%" y="74%" numero="+")
          p El desarenador siempre debe ser del tipo autolimpiante. Para esto, la estructura deber tener tolvas en el fondo con pendiente superior a la inercia del material silceo para permitir la evacuacin hidrulica del material depositado en dichas estructuras.

        .tarjeta.color-acento-contenido.p-3(x="62.5%" y="62%" numero="+")
          p Las tuberas o canales de rebose y/o limpieza se unirn a una tubera o canal de descarga, los cuales deben tener un dimetro o ancho no menor de 0.25 m y/o una pendiente no menor del 2 %. 

        .tarjeta.color-acento-contenido.p-3(x="77%" y="54%" numero="+")
          p Debe ubicarse una caja de inspeccin en la tubera de limpieza adyacente o lo ms cerca posible de la descarga de arenas (p.86).

    .row.bg-titulo4.align-items-center.p-1.mb-4
      h3.text-white.my-3.texto-derecha Coagulacin


    .row.justify-content-center.align-items-center.mb-5
      .col-lg-9
        p #[b El RAS 2000, ttulo C], establece que los procesos que se llevan a cabo en esta etapa del tratamiento del agua potable son la dosificacin y la mezcla rpida. 

        .cajon.color-gris.p-4
          p.mb-0 Una vez adicionados, los coagulantes y auxiliares de la coagulacin deben dispersarse rpida y homogneamente en el cuerpo de agua, para lo cual deben emplearse las unidades de mezcla rpida. Estos equipos pueden ser hidrulicos o mecnicos.

      .col-lg-3
        figure
          img(src='@/assets/curso/temas/tema1/img47.svg', alt='Imgen decorativa')        


    .row.justify-content-center.align-items-center.mb-5
      p Entre las unidades hidrulicas de mezcla rpida que pueden usarse se encuentran el resalto hidrulico, los vertederos, los mezcladores estticos y los difusores; entre las unidades mecnicas de mezcla rpida que pueden emplearse se encuentran los mezcladores mecnicos. Para los niveles de complejidad de sistema bajo y medio, no se recomienda el empleo de mezcladores mecnicos sino hidrulicos.

      p Para garantizar que la coagulacin - floculacin est realizndose correctamente, deben verificarse los siguientes parmetros:

      .col-lg-3
        figure
          img(src='@/assets/curso/temas/tema1/img48.svg', alt='Imgen decorativa')

      .col-lg-9
        ul.lista-ul--color.lista-ul--separador.fa-ul2.mb-3
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | La eficiencia de remocin de la turbiedad en el agua decantada o filtrada, si se trata de un tratamiento por filtracin directa.
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | La eficiencia de remocin del color en el agua decantada o filtrada, o del carbono orgnico total (COT).
          li.l-p 
            i.fas.fa-check-circle.color-vineta1
            | La concentracin residual de Al (+++) o Fe (+++) del agua filtrada o efluente de la planta, la cual debe cumplir con los valores mximos aceptables establecidos en Resolucin 2115 de 2007, o la norma que la modifique o reemplace.

        .cajon.color-gris.p-4
          p.mb-0 De la eficiencia de este proceso dependen los rendimientos de todos los tratamientos posteriores al agua, como la sedimentacin, la filtracin y la desinfeccin.

    .row.justify-content-center.align-items-center.mb-5(data-aos="flip-down")
      .col-lg-10
        .tarjeta.bgDescarga.p-4
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/temas/tema1/img21.svg",alt="Imgen decorativa")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  h3.texto-blanco.mb-3 Reglamento tcnico del sector de agua y saneamiento. Titulo C.
                  p.texto-blanco Para identificar todas las condiciones de operacin de la coagulacin, lo invitamos a leer el documento Potabilizacin de agua el cual se encuentra en el material complementario,  se sugiere su descarga.
                .col-sm-auto
                  a.boton.color-primario(href="https://www.catorce6.com/images/legal/Titulo_C_Potabilizacion.pdf" target="_blank")
                    span.texto-negro Descargar
