    .row.bg-titulo4.align-items-center.p-1.mb-4
      h3.text-white.my-3.texto-derecha Floculacin


    .row.justify-content-center.align-items-center.mb-5
      p #[b El RAS 2000, ttulo C,] establece que en el proceso de floculacin pueden emplearse los floculadores hidrulicos, mecnicos o hidromecnicos, veamos estos tipos de floculadores de manera detallada: 

    .row.justify-content-center.align-items-center.mb-5
      TabsC.color-acento-contenido
        .py-3.py-md-4(titulo="Floculadores hidrulicos")
          .row.justify-content-center.align-items-center
            .col-md-8.mb-4.mb-md-0

              p Utilizan el cambio de direccin de flujo del agua, inducido por diferentes mecanismos, para producir la turbulencia necesaria y promover la formacin del floc, adems derivan su energa de la carga de velocidad que el lquido adquiere en su trnsito por un conducto. Entre los floculadores hidrulicos que pueden ser implementados, se encuentran: 

              .row
                .col-lg-6
                  ul.lista-ul--color.fa-ul2.mb-3
                    li
                      i.fas.fa-check-circle.color-vineta1
                      | Flujo horizontal. 
                    li
                      i.fas.fa-check-circle.color-vineta1
                      | Flujo vertical.
                    li
                      i.fas.fa-check-circle.color-vineta1
                      | El floculador Alabama. 
                
                .col-lg-6
                  ul.lista-ul--color.fa-ul2.mb-3
                    li
                      i.fas.fa-check-circle.color-vineta1
                      | Flujo helicoidal. 
                    li
                      i.fas.fa-check-circle.color-vineta1
                      | El floculador de lechos porosos. 

            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema1/img49.png', alt='Imgen decorativa')

        .py-3.py-md-4(titulo="Floculadores mecnicos")
          .row.justify-content-center.align-items-center
            .col-md-8

              p Requieren de un equipo electromecnico para mover un agitador de paletas. En el nivel del sistema bajo, se deben evitar este tipo de floculadores.   

              ul.lista-ul--color.fa-ul2.mb-3
                li
                  i.fas.fa-check-circle.color-vineta1
                  p.mb-0 <b>Giratorios,</b> la agitacin se produce por el giro de paletas alrededor de un eje horizontal o vertical. 
                li
                  i.fas.fa-check-circle.color-vineta1
                  p.mb-0 <b>De turbina,</b> la agitacin se produce mediante grupos de turbinas dispuestos a lo largo de un eje horizontal que se instalan en tanques alargados.
                li
                  i.fas.fa-check-circle.color-vineta1
                  p <b>Reciprocantes,</b> la agitacin se produce por desplazamiento vertical hacia arriba y abajo de unas parrillas dentro del agua. 

            .col-md-4.mb-4.mb-md-0
              figure
                img(src='@/assets/curso/temas/tema1/img50.png', alt='Imgen decorativa')

        .py-3.py-md-4(titulo="Floculadores hidromecnicos")
          .row.justify-content-center.align-items-center
            .col-md-8

              p Utilizan la energa hidrulica a la entrada para mover una turbina de impulso, la cual gira con baja cabeza hidrulica. Esta transmite su movimiento a su eje, el cual va en posicin horizontal a travs de poleas y correas de poliuretano.

              p Es una solucin intermedia entre floculadores mecnicos e hidrulicos, no consume energa elctrica y se adapta a ciertas configuraciones de la planta de tratamiento, se aprovecha el agua para producir la cabeza hidrulica suficiente y mover la rueda Pelton.


            .col-md-4.mb-4.mb-md-0
              figure
                img(src='@/assets/curso/temas/tema1/img51.svg', alt='Imgen decorativa')

    .row.bg-titulo4.align-items-center.p-1.mb-5
      h3.text-white.my-3.texto-derecha Sedimentador

    .row.justify-content-center.align-items-center.mb-5
      .col-10.col-lg-2
        figure
          img(src='@/assets/curso/temas/tema1/img52.png', alt='Imgen decorativa').img-52-tm1

      .col-lg-10
        .tarjeta-gris.p-4
          .row.justify-content-center.align-items-center
            .col-lg-2
            .col-lg-10
              p El proceso de sedimentacin debe realizarse siempre que se tiene que producir coagulacin de barrido o por adsorcin, para poder remover la turbiedad.

              p.mb-0 En otros casos es opcional de acuerdo con los ensayos de laboratorio o planta piloto. 

    .row.mb-3
      p La unidad de sedimentacin debe constar de: 

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8
        figure
          img(src='@/assets/curso/temas/tema1/img53.svg', alt='Muestra cuatro zonas:<br>1. Zona de entrada.<br>2. Zona de sedimentacin.<br>3. Zona de recoleccin de lodos.<br>4. Zona de salida.')      


    .row.justify-content-center.align-items-center.mb-5
      .col-lg-9
        .cajon.color-gris.p-4.mb-4
          p.mb-0 Los sedimentadores, que pueden emplearse son el de flujo horizontal y flujo vertical. Tambin puede realizarse la sedimentacin en unidades con manto de lodos, los que a su vez se dividen en sedimentadores de manto de lodos de suspensin hidrulica y sedimentadores de manto de lodos de suspensin mecnica. Puede adems emplearse los sedimentadores de alta tasa.

        p.mb-3 Para los niveles de complejidad de sistema bajo y medio se acepta el empleo del sedimentador de flujo horizontal o de alta tasa. En estos niveles no se aceptan en ningn caso los sedimentadores de manto de lodos, de suspensin mecnica o hidrulica. Para todos los niveles de complejidad del sistema, deben realizarse estudios de tratabilidad en el laboratorio y/o planta piloto para determinar los parmetros de diseo.

      .col-10.col-lg-3
        figure
          img(src='@/assets/curso/temas/tema1/img54.svg', alt='Imgen decorativa')       

    .row.bg-titulo4.align-items-center.p-1.mb-5
      h3.text-white.my-3.texto-derecha Filtracin

    .row.justify-content-center.align-items-center.mb-5
      .col-10.col-lg-4.mb-4
        figure
          img(src='@/assets/curso/temas/tema1/img55.svg', alt='Imgen decorativa')

      .col-lg-8 
        p #[b El RAS (2000), en su Ttulo C,] establece que en el proceso convencional la filtracin puede ser rpida o lenta. La filtracin rpida se divide en filtracin ascendente o descendente y puede filtrarse por gravedad o por presin. El lavado del medio filtrante puede ser intermitente o continuo. Tambin puede emplearse la filtracin lenta sola o con diversas etapas de prefiltracin.

        .cajon.color-gris.p-4.mb-4
          p.mb-0 El medio filtrante granular convencional puede estar constituido por arena, antracita, arena de alta densidad, granate, ilmenita o carbono activado granular, generalmente dispuesto sobre un lecho de grava.

        p A continuacin, se establecen los principales mtodos que pueden emplearse para controlar la tasa de filtracin, esto es:

    .bg_img_01
      .row.justify-content-center.mb-5
        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema1/img56.svg", alt="Imgen decorativa").img90.m-auto
            h3.text-center Prdida de carga variable
            p.text-center El afluente debe entrar al filtro por debajo del nivel de la canaleta de lavado. Debe contar con un vertedero de control, una vlvula que evite el vaciado del filtro al comienzo de las carreras.

        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema1/img57.svg", alt="Imgen decorativa").img90.m-auto
            h3.text-center Filtracin afluente y nivel variable 
            p.text-center El caudal debe distribuirse por igual mediante un vertedero de entrada sobre cada filtro, el vertedero de control debe quedar en un nivel superior al nivel mximo, la unidad debe ser profunda para permitir una variacin del nivel, debe introducirse un sistema de disipacin de energa.

        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema1/img58.svg", alt="Imgen decorativa").img90.m-auto
            h3.text-center Filtracin de tasa constante
            p.text-center Es el tipo de control tradicional de los filtros rpidos convencionales. Debe emplearse un controlador variable que mantenga una prdida de carga total constante al abrirse gradualmente la vlvula, a medida que el lecho se colmata. 



    .row.bg-titulo4.align-items-center.p-1.mb-5
      h3.text-white.my-3.texto-derecha Desinfeccin

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.mb-4
        p #[b El RAS (2000), en ttulo C], establece que entre los procesos de desinfeccin que pueden realizarse est primordialmente la cloracin, incluidos sus derivados, el dixido de cloro, los hipocloritos y procedimientos como el de la cloraminacin. Como desinfectantes complementarios se tienen el ozono y los rayos ultravioleta. 

        .cajon.color-gris.p-4
          p.mb-0 Para la desinfeccin por cloracin, deben emplearse tanques de contacto en los niveles de complejidad de sistema medio alto y alto; en los otros niveles es opcional. El tanque debe proporcionar el tiempo necesario que garantice la desinfeccin del agua. No se recomienda el uso de cloro gaseoso en el nivel de complejidad del sistema bajo. Para este nivel se recomienda el empleo de compuestos en estado slido o lquido, como: cal clorada, hipoclorito de calcio e hipoclorito de sodio. Todas las recomendaciones que aqu se dan son aplicables a los cuatro niveles de complejidad del sistema, a no ser que se especifique lo contrario.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema1/img59.png', alt='Imgen decorativa')      


    .row.bg-titulo3.align-items-center.p-1.mb-5
      h3.text-white.my-3.texto-derecha Estabilizacin y ablandamiento

    .row.justify-content-center.align-items-center.mb-5
      p.mb-4 #[b El RAS en su ttulo C], tambin determina que el agua tratada a la salida de la planta no debe presentar propiedades corrosivas con respecto a las tuberas del sistema de distribucin y del interior de las viviendas; el pH del agua debe ser prximo al de saturacin. Si es necesario hacer uso de inhibidores que a la vez pueden ayudar a proteger la red de distribucin de agua de la formacin de biopelculas en su interior, estos pueden utilizarse si son econmicamente factibles.

      p Para proteger las tuberas de los agentes corrosivos presentes puede ajustarse la composicin del agua, pH, concentracin en iones de calcio y la alcalinidad al valor del equilibrio de saturacin del carbonato de calcio para una temperatura del agua dada. El agua ideal debe tener una dureza total entre 40 mg/L y 60 mg/L de equivalente de carbonato de calcio (CaCO<sub>3</sub>), a un pH y alcalinidad que alcance las condiciones de saturacin y una dureza de magnesio de no ms que 36 mg/L para minimizar la formacin de hidrxido de magnesio a temperaturas elevadas.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema1/img60.svg', alt='Imgen decorativa')

      .col-lg-8
        .cajon.color-gris.p-4.mb-3 
          p.mb-0 Por medio del ablandamiento del agua debe reducirse el contenido de minerales disueltos, particularmente calcio y magnesio que son los mayores impulsores de la dureza del agua, otros iones que pueden producir dureza son el hierro, manganeso, estroncio, bario, zinc y aluminio, de cualquier modo, estos iones no estn presentes en cantidades significantes. 
          
        p Los beneficios que deben obtenerse con el ablandamiento del agua son los siguientes: 

        .row.justify-content-center.align-items-center
          .col-lg-5
            ul.lista-ul--color.lista-ul--separador.fa-ul2.mb-3
              li.l-p
                i.fas.fa-check-circle.color-vineta2
