              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Un ligero grado de remocin de trazas orgnicas. 
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Economa de jabn y detergentes. 
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Mejor lavado de ropa y utensilios domsticos.
          .col-lg-1
          .col-lg-5
            ul.lista-ul--color.lista-ul--separador.fa-ul2.mb-3
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Disminucin de incrustaciones en artefactos domsticos. 
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Mejor coccin y preparacin de alimentos.
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Si se opera en forma correcta, puede prevenir la corrosin. 
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Incremento en la eficacia de la filtracin. 

    .row.justify-content-center.align-items-center.mb-5
      p Debe utilizarse el proceso de estabilizacin si es econmicamente factible y si la calidad del agua lo permite, en caso contrario, debe emplearse otro mtodo que est comprobado eficientemente para controlar los problemas de corrosin en la red.



